
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent, ref } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { HtsReferralService } from "@/apps/HTS/services/hts_referral_service"
import { HtsCircumcisionService } from '../../services/hts_circumcision_service'
import { Offline } from '@/composables/offline'
import { convertArrayToString } from '@/utils/Strs'
import HisStandardForm from "@/components/Forms/HisStandardForm.vue";
import { StartHtsEncounter } from "@/apps/HTS/composables/HtsFormEncounter";

export default defineComponent({
  components: { IonPage, HisStandardForm },
  setup() {
    const service = new HtsReferralService(-1)
    const fields = ref<Field[]>([])
    const isHivPositive = ref(false)
    const {
      visitID,
      getEncounter,
      saveEncounter, 
      patientDashboardUrl,
      htsClient 
    } = StartHtsEncounter({
      onInitVisit: () => {
        fields.value = [
          referralServicesField(),
          commentsField()
        ]
      }
    })

    function onFinish(_: any, computedData: any) {
      saveEncounter({
        encounterTypeID: service.encounterTypeID,
        encounterName: "REFERRAL",
        computedData
      })
    }

    function getObs(concept: string) {
      return HtsReferralService.getFirstValueCoded(visitID.value as any, concept)
    }

    function referralServicesField(): Field {
        let recommendCircumcision = false
        const knownSTIs: string[] = []
        return {
          id: "services",
          helpText: 'Refer Client to other Services?',
          type: FieldType.TT_MULTIPLE_SELECT,
          isRequired: () => true,
          init: async () => {
            const age = htsClient.age
            if (htsClient.gender === 'M') {
              const circumcisionEncounter = getEncounter('CIRCUMCISION')
              if (circumcisionEncounter) {
                recommendCircumcision = /no/i.test(circumcisionEncounter['Circumcision status']) && age > 14 && age < 64
              } else if(!Offline().isOfflineMode.value) {
                const circumcision = new HtsCircumcisionService(visitID.value as any)
                const status = (await circumcision.getRecentCircumcisionStatus())
                recommendCircumcision = [status === 'No', age > 14 && age < 64].every(Boolean)
              }
            }
            const stis = ['Hepatitis B Test Result', 'Syphilis Test Result'] 
            const testingEncounter = getEncounter('Testing');
            if (testingEncounter) {
              stis.forEach((sti) => {
                if (testingEncounter[sti] === 'Positive') knownSTIs.push(sti)
              })
              isHivPositive.value = /positive/i.test(`${testingEncounter['HIV status']}`)
            } else if (!Offline().isOfflineMode.value) {
              for(const sti of stis) {
                if ((await getObs(sti)) === 'Positive') knownSTIs.push(sti)
              }
              isHivPositive.value = ['Positive', 'Positive Initial Professional'].includes((await getObs('HIV status')))
            }
            return true
          },
          finalComputedValue: (v: Option[]) => {
            return {
              obs: v.map(d => service.buildValueText('Referrals ordered', `${d.value}`))
            }
          },
          onValueUpdate(listData: Option[], value: Option) {
            return listData.map(l => {
              if (value.value === 'None' && l.value != 'None') {
                l.isChecked = false
              }
              if (value.value != 'None' && value.isChecked && l.value === 'None') {
                l.isChecked = false
              }
              return l
            })
          },
          options: (f: any) => {
            const options: any = []
            const opt = (v: string, options = {} as any) => {
              const selected = f.services ? f.services.some((d: Option) => d.value === v) : false
              return { label: v, value: v, isChecked: selected, ...options }
            }
            options.push(opt('STI', knownSTIs.length ? {
              description: {
                text: `Client tested positive for ${convertArrayToString(knownSTIs)}. Please recommend this option if client has not started yet`
              }
            } : {}))
            options.push(opt('TB'))
            if (htsClient.gender === 'M') {
              options.push(opt('VMMC', recommendCircumcision ? {
                description: {
                  text: 'Client is not circumcised, please recommend this option'
                }
              } : {}))
            }
            options.push(opt('PrEP ', isHivPositive.value ? {
              disabled: isHivPositive.value,
              description: {
                text: 'Client is HIV positive, cannot refer for PrEP'
              }
            } : {}))
            options.push(opt('PEP ', isHivPositive.value ? {
              disabled: isHivPositive.value,
              description: {
                text: 'Client is HIV positive, cannot refer for PEP'
              }
            } : {}))
            options.push(opt('Other'))
            options.push(opt('None'))
            return options
          },
          config: {
            footerBtns: [
              {
                name: "None",
                slot: "end",
                onClickComponentEvents: {
                  refreshOptions: (_: any, listData: Option[]) => {
                    return listData.map(o => {
                      o.isChecked = o.label === 'None'
                      return o
                    })
                  }
                },
                onClick: () => 'None'
              }
            ]
          }
        }
      }

    function commentsField(): Field {
      return {
        id: 'comment',
        helpText: 'Comments (Optional)',
        type: FieldType.TT_NOTE,
        finalComputedValue: (v: Option | null) => {
          if (v && !/^\s*$/gi.test(`${v.value}`)) {
            return {
              obs: service.buildValueText('Comments', `${v.value}`)
            }
          }
          return null
        }
      }
    }
  
    return {
      patientDashboardUrl,
      onFinish,
      fields
    }
  }
})
